"use client";

import { ReactNode } from "react";
import dynamic from "next/dynamic";
import { useSearchParams } from "next/navigation";
import { Skeleton } from "@/components/ui/skeleton";

// Dynamic import with no SSR to avoid hydration mismatch
const TopBar = dynamic(
  () => import("./TopBar").then((mod) => mod.TopBar),
  { ssr: false, loading: () => <TopBarSkeleton /> }
);

const MobileBottomNav = dynamic(
  () => import("./TopBar").then((mod) => mod.MobileBottomNav),
  { ssr: false }
);

function TopBarSkeleton() {
  return (
    <header className="sticky top-0 z-50 w-full border-b border-border bg-background">
      <div className="flex h-14 items-center px-4 gap-4">
        <Skeleton className="w-8 h-8 rounded-lg" />
        <Skeleton className="h-4 w-16 hidden sm:block" />
        <div className="hidden md:flex items-center gap-2 flex-1">
          {[...Array(5)].map((_, i) => (
            <Skeleton key={i} className="h-8 w-20 rounded-lg" />
          ))}
        </div>
        <div className="ml-auto flex items-center gap-2">
          <Skeleton className="h-8 w-8 rounded-full" />
        </div>
      </div>
    </header>
  );
}

interface DashboardLayoutProps {
  children: ReactNode;
  experienceId: string;
  user?: {
    id: string;
    name: string | null;
    avatar: string | null;
    sellerProfile?: { id: string } | null;
  } | null;
}

export function DashboardLayout({
  children,
  experienceId,
  user,
}: DashboardLayoutProps) {
  const searchParams = useSearchParams();
  const currentTab = searchParams.get("tab") || "browse";
  const mode = searchParams.get("mode") || "buyer";

  return (
    <div className="min-h-screen bg-background">
      <TopBar experienceId={experienceId} user={user} />
      <main className="flex-1 pb-20 md:pb-0">
        {children}
      </main>
      <MobileBottomNav
        experienceId={experienceId}
        currentTab={currentTab}
        mode={mode}
      />
    </div>
  );
}
